.pokeball {
    position: absolute;
    height: 50px;
    width: 50px;
    -webkit-animation: pkgif 1.14s steps(27) infinite;
    animation: pokeball 1.14s steps(27) infinite;
}

@-webkit-keyframes pokeball {
    from {
        background-position: 0 0
    }
    to {
        background-position: -1350px 0
    }
}

@keyframes pokeball {
    from {
        background-position: 0 0
    }
    to {
        background-position: -1350px 0
    }
}
.loader-overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: calc(100% - 64px);
    top: 64px;
    background-color: rgba(0,0,0,0.2);
    z-index: 2;
}

.loader {
    height: 100px;
    width: 100px;
    position: absolute;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    -webkit-align-items: center;
    flex-direction: column;
    justify-content: center;
    -webkit-justify-content: center;
    -webkit-transition: background-color 150ms;
    transition: background-color 150ms;
}

.loader img {
    -webkit-animation: spin 1s linear infinite;
    animation: spin 1s linear infinite;
    margin: 0 auto;
    width: 100px;
    text-align: center;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
}

.loader p {
    color: #fff;
    font-weight: 700;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
.pokemon-card__container{
    max-width: 345px;
    margin: 15px;
    min-width: 300px;
}

.pokemon-card__media{
    background-size: 50% !important;
    height: 150px;
}

.pokemon-card__chips-container{
    margin-top: 10px;
}

.pokemon-card__chip{
    margin: 0 5px;
}

.pokemons-list__pokemons-container{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    width: calc(100% - 100px);
    text-align: center;
    margin: auto;
    max-width: 1650px;
}


.pokemons-list__controls-container {
    text-align: center;
    padding-top: 10px;
    max-width: 1620px;
    margin: auto;
    width: calc(100% - 100px);
}

@media screen and (max-width: 1750px) {
    .pokemons-list__pokemons-container {
        width: 100%
    }
}

@media screen and (max-width: 700px) {
    .pokemons-list__controls-container {
        width: calc(100% - 20px)
    }
}


.pokemons-list__page-controls-container {
    float: right;
}

.pokemons-list__filter-controls-container {
    float: left;
}

.pokemons-list__page-button {
    margin: 0 10px !important;
}

.pokemons-list__control{
    margin-right: 10px !important;
    min-width: 70px !important;
}

.pokemon-profile__container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}


.pokemon-profile__media{
    background-size: 60% !important;
    height: 240px;
}


.pokemon-profile__left-column-container{
    width: 350px;
}
.pokemon-profile__right-column-container{
    max-width: 630px;
}


.pokemon-profile__paper-heading{
    margin-bottom: 10px !important;
}
.pokemon-profile__paper-container{
    margin: 15px;
    padding: 16px;
}


.pokemon-profile__detail-container{
    height: 20px;
    margin: 3px 0;
}
.pokemon-profile__detail-title{
    width: 50%;
    float: left;
}
.pokemon-profile__detail-text{
    width: 50%;
    float: right;
}


.pokemon-profile__stat-container{
    margin: 8px 0;
}


.pokemon-profile__sprite{
    width: 140px;
}
body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
    text-transform: capitalize;
}

a {
    text-decoration: none;
}

/*Styles for scrollbars*/
::-webkit-scrollbar {
    width: 6px;
    height: 6px
}

::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #F5F5F5;
}

::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
}

.lowercase-text{
    text-transform: lowercase;
}

